.AppBar {
    height: 5em;
    background-color: #f0f3f5;
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    box-shadow: 1px 2px 3px;
    h1 {
        margin-left: 2em;
        font-family: sans-serif; }
    input {
        margin-right: 2em;
        border: none;
        border-radius: 1em;
        background-color: grey;
        padding: 1em; }
    input::placeholder {
        color: white;
        opacity: 0.7; } }
